@charset "UTF-8";
.comment-box {
  width: 370px;
  height: 300px;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  z-index: 999;
  padding: 10px;
  left: 50%;
  top: 50%;
  margin: -80px 0 0 -150px;
  border-radius: 15px; }
  .comment-box .text-wrap::after {
    content: "";
    display: block;
    clear: both; }
  .comment-box textarea {
    padding: 8px;
    width: 350px;
    border: 1px solid #CCC;
    height: 100px;
    outline: 0;
    display: block;
    resize: none;
    font-size: 14px; }
  .comment-box .text-len {
    float: right;
    margin-top: -20px;
    margin-right: 10px;
    color: #666;
    font-weight: bold;
    user-select: none; }
  .comment-box .file-input {
    cursor: pointer;
    font-family: sans-serif;
    width: 70px;
    height: 20px;
    margin: 0;
    padding: 0;
    background: 0 0; }

.cb-err {
  color: #ca5252;
  font-size: 12px;
  text-align: center; }

.cb-com-wrap {
  padding: 10px 0 20px 0; }
  .cb-com-wrap::after {
    content: "";
    display: block;
    clear: both; }
  .cb-com-wrap .cb-com-0, .cb-com-wrap .cb-com-1, .cb-com-wrap .cb-com-2 {
    float: left;
    width: 30%;
    position: relative; }
    .cb-com-wrap .cb-com-0 input, .cb-com-wrap .cb-com-1 input, .cb-com-wrap .cb-com-2 input {
      vertical-align: text-bottom;
      cursor: pointer; }
    .cb-com-wrap .cb-com-0 label, .cb-com-wrap .cb-com-1 label, .cb-com-wrap .cb-com-2 label {
      cursor: pointer;
      display: inline-block;
      font-size: 0;
      width: 20px;
      height: 20px;
      background: url("http://127.0.0.1:5000/image/ui/comments.png") no-repeat;
      position: absolute;
      top: 0;
      left: 13px;
      padding-left: 10px; }
  .cb-com-wrap .cb-com-0 label {
    background-position: 10px -101px; }
  .cb-com-wrap .cb-com-1 label {
    background-position: 10px -51px; }
  .cb-com-wrap .cb-com-2 label {
    background-position: 10px 0; }

.cb-img-wrap {
  margin-top: 10px; }
  .cb-img-wrap::after {
    content: "";
    display: block;
    clear: both; }
  .cb-img-wrap .cb-file {
    float: left;
    position: relative;
    display: inline-block;
    border-radius: 4px;
    overflow: hidden;
    text-decoration: none;
    line-height: 25px;
    cursor: pointer;
    margin-top: 8px; }
    .cb-img-wrap .cb-file input {
      position: absolute;
      top: 0;
      left: 0;
      width: 60px;
      height: 25px;
      opacity: 0;
      cursor: pointer; }
    .cb-img-wrap .cb-file span {
      display: inline-block;
      background: #6868e8;
      color: #fff;
      width: 60px;
      height: 25px;
      text-align: center;
      cursor: pointer; }
  .cb-img-wrap .cb-pic-wrap {
    float: left; }
    .cb-img-wrap .cb-pic-wrap::after {
      content: "";
      display: block;
      clear: both; }
    .cb-img-wrap .cb-pic-wrap .cb-pic-item {
      float: left;
      height: 40px;
      width: 40px;
      text-align: center;
      vertical-align: middle;
      line-height: 40px;
      border: 1px solid #ccc;
      background-color: #ddd;
      margin-left: 5px;
      position: relative; }
      .cb-img-wrap .cb-pic-wrap .cb-pic-item:hover::after {
        content: "删除";
        display: block;
        width: 40px;
        height: 40px;
        background: rgba(0, 0, 0, 0.4);
        color: #fff;
        cursor: pointer;
        position: absolute;
        top: 0;
        left: 0; }
  .cb-img-wrap .cb-pic-last {
    float: left;
    line-height: 40px;
    color: #eee;
    margin-left: 5px; }

.cb-btn-wrap {
  margin-top: 20px; }
  .cb-btn-wrap::after {
    content: "";
    display: block;
    clear: both; }

.cb-btn-sub, .cb-btn-cancel {
  float: left;
  width: 100px;
  height: 36px;
  text-align: center;
  line-height: 36px;
  cursor: pointer;
  border-radius: 5px; }

.cb-btn-sub {
  color: #fff;
  background-color: #3498db;
  margin-left: 60px; }

.cb-btn-cancel {
  background-color: #c3deff;
  color: #695d5d;
  margin-left: 20px; }
